/* utilities for display components in special width */
@media screen and (max-width: 601px) {
    .tablet {
        display: none !important; /* show element on tablet and desktop */
    }
}

@media screen and (max-width: 993px) {
    .desktop {
        display: none !important; /* show element only on desktop */
    }
}

@media screen and (min-width: 601px) {
    .mobile {
        display: none !important; /* show element only on mobile */
    }
}

/* Flex container properties */
.flex {
    --gap: 0;
    display: flex;
    gap: var(--gap);
}

.inline {
    display: inline-flex;
}

.responsive {
    flex-wrap: wrap;
}

@media (min-width: 601px) {
    .responsive {
        flex-wrap: nowrap;
    }
}

.flex.dir-c {
    flex-direction: column;
}

.flex.items-center{
    align-items: center;
}

.flex.items-start{
    align-items: flex-start;
}

.flex.items-end{
    align-items: flex-end;
}

.flex.justify-center {
    justify-content: center;
}

.flex.justify-between {
    justify-content: space-between;
}

.flex.gap-1 {
    --gap: .3rem;
}

.flex.gap-2 {
    --gap: .6rem;
}

.flex.gap-3 {
    --gap: .9rem;
}

.flex.gap-4 {
    --gap: 1.2rem;
}

.flex.gap-5 {
    --gap: 1.5rem;
}

.flex.gap-y-1 {
    row-gap: .3rem;
}

.flex.gap-y-2 {
    row-gap: .6rem;
}

.flex.gap-y-3 {
    row-gap: .9rem;
}

.flex.gap-y-4 {
    row-gap: 1.2rem;
}

.flex.gap-y-5 {
    row-gap: 1.5rem;
}

.flex.gap-y-6 {
    row-gap: 1.8rem;
}

.flex.gap-y-7 {
    row-gap: 2.1rem;
}

.flex.gap-y-8 {
    row-gap: 2.4rem;
}

.flex.wrap {
    flex-wrap: wrap;
}

/* flex children properties */

.f-item {
    width: 100%;
}

.f-item.s1 {
    width: 8.33333%
}

.f-item.s2 {
    width: 16.66666%
}

.f-item.s3 {
    width: 24.99999%
}

.f-item.s4 {
    width: 33.33333%
}

.f-item.s5 {
    width: 41.66666%
}

.f-item.s6 {
    width: 49.99999%
